import { TransformToolComponent } from 'src/app/core/transform-tool/transform-tool.component';
import generateSchema from 'generate-schema';
import { NgModule } from '@angular/core';
import { CoreModule } from '../../core/core.module';
import { RouterModule } from '@angular/router';
import * as readme from './readme.md';

export class JsonToSchemaComponent extends TransformToolComponent {
    id = 'json-to-schema';
    action = 'Create Schema';
    schema = {
        type: 'object',
        properties: {
            input: { type: 'string', title: 'JSON', description: 'JSON to create schema from' },
            name: { type: 'string', description: 'Name of schema created' }
        },
        required: ['input', 'name']
    };
    onTransform(transform:any): string {
        var schema = generateSchema.json(transform.name, JSON.parse(transform.input));
        return JSON.stringify(schema, null, 3);
    };
    documentation = readme.default
}

@NgModule({
    declarations: [
        JsonToSchemaComponent,
    ],
    imports: [
        CoreModule,
        RouterModule.forChild([
            { path: '', component: JsonToSchemaComponent }
        ])
    ]
  })
export class JsonToSchemaModule { }